* {
    box-sizing: border-box;
}
body {
    margin: 0;
}
.container {
    display: grid;
    grid-template-columns: .20fr 1fr;
    grid-template-rows: auto;
    grid-template-areas: 
    "details profile"
    "details work"
    "skills education"
    "skills education";
}

.details-div {
    grid-area: details;
    min-height: 100%;
    padding: 15px;
     display: flex;
     align-items: center;
     flex-direction: column;
}
.skills-div {
    grid-area: skills;
    padding: 15px;
     display: flex;
     flex-direction: column;
    align-items: center;
}
.general-info-div {
    grid-area: profile;
    padding: 15px;
}
.education-main-div {
    grid-area: education;
    padding: 15px;
}
.work-experience-div {
    grid-area: work;
    padding: 15px;
}
.left-col {
    background-color: rgba(158, 158, 212, 0.7);
}
h1 {
    margin: 15px 0;
    border-bottom: 1px solid black;
    padding-bottom: 3px;
}

.profile-image {
    width: 60px;
    border-radius: 50%;
    margin: 0 auto;
}
.ul {
    list-style: none;
    padding: 0;
}
.ul li {
    padding: 8px
}
.details-div span, a {
    padding: 15px 0;
}

.date {
    font-style: italic;
    margin-bottom: 10px;
}

.form-div form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form-div .input {
    height: 40px;
    width: 50%;
    margin-bottom: 10px;
}
.form-div textarea {
    width: 50%;
    height: 6rem;
}

.form-div .submit {
    width: 10rem;
    height: 3rem;
    margin-top: 20px;
    margin-bottom: 150px;
    background-color: rgba(158, 158, 212, 0.7);
    color: white;
    border-color: white;
    border-radius: 2rem;
    border-style: none;
    box-shadow:3px 4px 3px 1px rgba(0,0,0,0.12) ;
}

.form-div .submit:hover {
    background-color: rgba(126, 126, 212, 0.7);
}

input[type=checkbox] {
    margin: 10px;
}